@import './../abstracts/variables';
a:-webkit-any-link {
    text-decoration: none;
    color: black;
}
.generic {
    background: $generic-bg;
    width: 100%;
    height: 2500px;
    font-family: monospace;
    position: absolute;


    .hero {
        width: 100%;
        position: relative;
        height: 700px;
        background-position: bottom;
        background-repeat: no-repeat;
        top: 0;
        background: #9d94c3;
        background: $purple;
        color: black;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        justify-content: center;
        padding: 0;
        @media (max-width: 748px){
            height: 360px;
        }

        .left {
            width: 65%;
            position: absolute;
            left: 0;
            top: 0;
            @media (max-width: 748px){
                width: 100%;
            }
            img {
                height: 600px;
                @media (max-width: 748px){
                    height: 320px;
                    margin-top: 20px;
                    margin: auto;
                }
            }
           
        }
        .right {
            width: 35%;
            position: absolute;
                bottom: 0;
                right: 0;
            img {
                height: 600px;
                margin-right: 0;
                
            }
        }

        @media (max-width: 740px){
            h1 {
                font-size: 28px;
            }
            h2 {
                font-size: 38px;
            }
        }
    }

    .work {
        padding-block: 100px;
        h2 {
            width: 100%;
            padding-bottom: 40px;
            font-size: 30px;
        }
        text-align: center;
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;

    }
    .skills {
        padding-block: 100px;
        h2 {
            width: 100%;
            padding-bottom: 0px;
            font-size: 30px;
            text-align: center;
        }
        display: flex;
        justify-content: space-evenly;
        gap: 10px;
        flex-wrap: wrap;
        font-size: 18px;
        ul {
            width: 25%;
            @media (max-width: 748px){
                width: 100%;
                padding-inline: 50px;
            }
        }
    }
}